<template>
    <el-container class="home-container" >
        <!--  -->
        <el-header ref="header">
            <div >
                <span></span>
            </div>
            <span>{{ userMsg.user_name }}</span>
            <el-button type="info" @click="logout"></el-button>
        </el-header>
        <el-container>
            <!--  -->
            <el-aside :width="isCollapse ? '64px' : '200px'"> <!--200px 64px-->
                <!--  -->
                <el-menu :class="status == false ? '' : 'fiedx'" background-color="#444444" text-color="#fff" active-text-color="#e37554"
                unique-opened :collapse="isCollapse" :collapse-transition="false" :router="true"
                :default-active="activePath">
                  <div class="toggle-button" @click="toggleCollapse"><a style="color:#fff;fontSize:14px">{{ btntext }}</a></div>
                <!-- collapse   collapse-transition  -->
                    <!--  -->
                    <el-submenu :index="index + ''" v-for="(item,index) in menulist" :key="item.name">
                        <!--  -->
                        <template slot="title">
                            <!--  -->
                            <i :class="iconsObj[index]"></i>
                            <!--  -->
                            <span>{{ item.name }}</span>
                        </template>
                        <!--  -->
                        <el-menu-item :index="subItem.nav" v-for="subItem in item.list" :key="subItem.nav" @click="saveNavState(subItem.nav)">
                            <template slot="title">
                            <!--  -->
                            <i class="el-icon-menu"></i>
                            <!--  -->
                            <span>{{ subItem.name }}</span>
                        </template>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <!--  -->
            <el-main>
                  <keep-alive include="AppUser,VirtualUser,SceneCrawling,AudioTagManage,AudioTag">
                    <router-view></router-view>
                  </keep-alive>
            </el-main>
        </el-container>
        <el-backtop :bottom="100"></el-backtop>
    </el-container>
</template>

<script>
export default {
  data () {
    return {
      // 
      menulist: [],
      iconsObj: {
        0: 'iconfont icon-yonghuguanli',
        1: 'iconfont icon-sucai-outline',
        2: 'iconfont icon-biaoqian1',
        3: 'iconfont icon-yonghuguanli1'
      },
      // 
      isCollapse: false,
      // 
      activePath: '',
      // 
      userMsg: {},
      // 
      btntext: '<',
      // 
      status: false
    }
  },
  created () {
    this.judgeAamin()
    this.activePath = window.sessionStorage.getItem('activePath')
    this.userMsg = JSON.parse(window.sessionStorage.getItem('userMsg'))
    this.getPath()
  },
  mounted () {
    window.addEventListener('scroll', this.headerScroll)
  },
  methods: {
    // 
    judgeAamin () {
      const status = JSON.parse(window.sessionStorage.getItem('userMsg'))
      if (status.status === 2) {
        // 
        this.menulist = [
          {
            name: '',
            list: [{
              name: '',
              nav: '/allUser'
            }, {
              name: '',
              nav: '/keyword'
            }]
          },
          {
            name: '',
            list: [{
              name: '',
              nav: '/allScene'
            }, {
              name: '',
              nav: '/sceneCrawling'
            }]
          },
          {
            name: '',
            list: [{
              name: '',
              nav: '/allTag'
            }]
          },
          {
            name: '',
            list: [{
              name: '',
              nav: '/allAccount'
            }]
          }
        ]
      } else {
        this.menulist = [
          {
            name: '',
            list: [{
              name: '',
              nav: '/allUser'
            }, {
              name: '',
              nav: '/keyword'
            }]
          },
          {
            name: '',
            list: [{
              name: '',
              nav: '/allScene'
            }, {
              name: '',
              nav: '/sceneCrawling'
            }]
          },
          {
            name: '',
            list: [{
              name: '',
              nav: '/allTag'
            }]
          }
        ]
      }
    },
    // 
    async getPath () {
      const { data: res } = await this.$http.get('/getPath')
      if (res.meta.status !== 200) return this.$message.error('')
      window.sessionStorage.setItem('path', JSON.stringify(res.data))
    },
    // 
    toggleCollapse () {
      this.isCollapse = !this.isCollapse
      if (this.isCollapse === true) {
        this.btntext = '>'
      } else {
        this.btntext = '<'
      }
    },
    //  indexsessionStorage
    saveNavState (activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    },
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/')
    },
    // 
    headerScroll () {
      const height = Number(parseInt(this.$refs.header.height))
      const scrollHeight = document.documentElement.scrollTop || document.body.scrollTop
      if (scrollHeight >= height) {
        this.status = true
      } else {
        this.status = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .home-container {
        height: 100%;
    }
    .el-header {
        background-color: #333333;
        display: flex;
        justify-content: space-between;
        padding-left: 0;
        align-items: center;
        color: #fff;
        font-size: 20px;
        > div {
            display: flex;
            align-items: center;
            span {
                margin-left: 15px;
            }
        }
    }
    .el-aside {
        background-color: #444444;
        .el-menu {
            border-right: none;
        }
    }
    .el-main {
        background-color: #EAEDF1;
    }
    .iconfont {
        margin-right: 10px;
        color: #fff;
    }
    .toggle-button {
        background-color: #535353;
        font-size: 10px;
        line-height: 24px;
        color: #fff;
        text-align: center;
        letter-spacing: 0.2em;
        cursor: pointer;
    }
    .el-backtop {
    background-color: rgb(160, 160, 160);
    color: #fff;
    width: 50px;
    height: 50px;
    text-align: center;
    font-size: 15px;
    padding: 5px;
    &:hover {
      background-color: rgb(110, 110, 110);
    }
  }

  .fiedx {
    position: fixed;
    top: 0;
  }
</style>
