<template>
    <div>
      <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
          <!--  -->
            <div class="content">
              <el-input class="description" placeholder="" v-model="description"></el-input>
              <div>
                <el-tag v-for="item in imagesInfo.tagPitch" @close="handleClose(item.id)" closable :key="item.id">{{ item.name }}</el-tag>
              </div>
              <div class="user_button">
                <el-button size="small" type="primary" icon="el-icon-paperclip" :disabled="btnShow" @click="principalShowInput"></el-button>
                <el-button size="small" type="primary" icon="el-icon-picture-outline" :disabled="btnShow" @click="addImagesUser"></el-button>
                <el-button  size="small" type="primary" icon="el-icon-video-play" :disabled="btnShow" @click="addVideoUser"></el-button>
                <el-button size="small" type="info" icon="el-icon-delete" :disabled="btnShow" @click="description = ''"></el-button>
                <el-input class="userInput" type="number" :disabled="btnShow" clearable placeholder="id" size="small" v-model="imagesInfo.user_id"></el-input>
                <el-input class="userInput" type="number" clearable :disabled="btnShow" placeholder="" size="small" v-model="datasum"></el-input>
              </div>
              <el-progress :percentage="schedule"></el-progress>
              <div :span="20" class="content_box">
                <p v-for="(item,index) in contentText" :key="index" class="content_msg">{{ item }}</p>
              </div>
            </div>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="addTagDialogVisible" width="50%" @close="addTagDialogClosed">
          <div class="tag_seace_content">
            <el-input @input="TagSearch" placeholder="" v-model="inputValue" clearable></el-input>
              <el-button type="primary" @click="addTagClick"></el-button>
          </div>
          <el-form ref="addTagRef" label-width="70px">
            <el-checkbox-group v-model="tagList.tagId" size="mini">
              <el-checkbox v-for="item in tagList.tagArr" :key="item.id" :label="item.id" border>{{ item.name }}</el-checkbox>
            </el-checkbox-group>
          </el-form>
        </el-dialog>
    </div>
</template>

<script>
export default {
  name: 'SceneCrawling',
  data () {
    return {
      // 
      imagesInfo: {
        user_id: '',
        tagPitch: [],
        data: {}
      },
      // 
      datasum: '',
      // 
      description: '',
      // 
      tagList: {
        tagArr: [],
        tagId: []
      },
      // 
      inputValue: '',
      // 
      addTagDialogVisible: false,
      // 
      pn: 0,
      // id
      searchId: '',
      // 
      schedule: 0,
      // 
      crawlingData: [],
      // 
      contentText: [],
      // 
      type: 1,
      // id
      user_id: '',
      // 
      btnShow: false,
      // 
      scene_index: 0,
      // 
      succeed: 0,
      // 
      errors: 0,
      // 
      timer: null,
      // 
      crawlingIndex: 0
    }
  },
  methods: {
    // 
    addTagDialogClosed () {
      this.$refs.addTagRef.resetFields()
    },
    // 
    principalShowInput () {
      this.inputValue = ''
      this.tagList = {
        tagArr: [],
        tagId: []
      }
      this.TagSearch()
      this.addTagDialogVisible = true
    },
    // 
    async TagSearch () {
      const { data: res } = await this.$http.get(`/sceneTagSearch?query=${this.inputValue}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.tagList.tagArr = res.data
    },
    // 
    addTagClick () {
      this.tagList.tagArr.forEach(item => {
        if (this.tagList.tagId.some(v => v === item.id)) {
          this.imagesInfo.tagPitch.push(item)
        }
      })
      this.tagList.tagArr = []
      this.tagList.tagId = []
      this.inputValue = ''
      this.addTagDialogVisible = false
    },
    // 
    handleClose (id) {
      const index = this.imagesInfo.tagPitch.findIndex(item => item.id === id)
      this.imagesInfo.tagPitch.splice(index, 1)
    },
    // 
    async addImagesUser () {
      if (this.datasum === '') {
        return this.$message.error('')
      } else if (this.imagesInfo.user_id === '') {
        return this.$message.error('id')
      } else if (this.description === '') {
        return this.$message.error('')
      }
      if (this.crawlingData.length === 0) {
        this.contentText = []
        this.schedule = 0
      }
      const { data: res } = await this.$http.get('/getImageReq', { params: { keyword: this.description, pn: this.pn } })
      if (res.meta.status !== 200) {
        this.btnShow = false
        this.succeed = 0
        this.errors = 0
        this.pn = 0
        this.crawlingData = []
        this.$message.error(res.meta.msg)
        this.contentText.unshift(res.meta.msg)
        return
      }
      this.crawlingData = res.data
      this.btnShow = true
      // 
      this.imageDownload()
    },
    async imageDownload (index) {
      if (this.succeed === Number(this.datasum)) {
        this.btnShow = false
        this.contentText.unshift(`${this.succeed}${this.errors}id${this.imagesInfo.user_id}`)
        this.succeed = 0
        this.errors = 0
        this.pn = 0
        this.crawlingData = []
        return
      }
      let i = index === undefined ? 0 : index
      if (i === this.crawlingData.length) {
        this.pn += 30
        this.addImagesUser()
        return
      }
      this.imagesInfo.data = this.crawlingData[i]
      const { data: res } = await this.$http.post('/setImagesReq', this.imagesInfo)
      if (res.meta.status === 404) {
        this.btnShow = false
        this.succeed = 0
        this.errors = 0
        this.pn = 0
        this.crawlingData = []
        this.$message.error(res.meta.msg)
        this.contentText.unshift(res.meta.msg)
      } else if (res.meta.status === 100) {
        this.contentText.unshift(`${this.crawlingData[i].fromPageTitleEnc}${this.crawlingData[i].middleURL}---`)
        this.imageDownload(++i)
      } else if (res.meta.status === 201) {
        this.contentText.unshift(`${this.succeed}${this.crawlingData[i].fromPageTitleEnc}${this.crawlingData[i].middleURL}---`)
        this.succeed++
        this.schedule = parseInt(this.succeed / Number(this.datasum) * 100)
        this.imageDownload(++i)
      } else {
        this.btnShow = true
        this.contentText.unshift(`${this.crawlingData[i].fromPageTitleEnc}${this.crawlingData[i].middleURL}--${res.meta.msg}`)
        this.errors++
        this.imageDownload(++i)
      }
    },
    // 
    async addVideoUser () {
      if (this.datasum === '') {
        return this.$message.error('')
      } else if (this.imagesInfo.user_id === '') {
        return this.$message.error('id')
      } else if (this.description === '') {
        return this.$message.error('')
      }
      if (this.crawlingData.length === 0) {
        this.contentText = []
        this.schedule = 0
        this.searchId = ''
      }
      const { data: res } = await this.$http.get('/getVideoReq', { params: { keyword: this.description, pn: this.pn, searchId: this.searchId } })
      if (res.meta.status !== 200) {
        this.btnShow = false
        this.succeed = 0
        this.errors = 0
        this.pn = 0
        this.crawlingData = []
        this.$message.error(res.meta.msg)
        this.contentText.unshift(res.meta.msg)
        return
      }
      this.crawlingData = res.data.feeds
      this.searchId = res.data.searchSessionId
      this.btnShow = true
      // 
      this.videoDownload()
    },
    async videoDownload (index) {
      if (this.succeed === Number(this.datasum)) {
        this.btnShow = false
        this.contentText.unshift(`${this.succeed}${this.errors}id${this.imagesInfo.user_id}`)
        this.succeed = 0
        this.errors = 0
        this.pn = 0
        this.searchId = ''
        this.crawlingData = []
        return
      }
      let i = index === undefined ? 0 : index
      if (i === this.crawlingData.length) {
        this.pn++
        this.addVideoUser()
        return
      }
      this.imagesInfo.data = this.crawlingData[i]
      const { data: res } = await this.$http.post('/setVideoReq', this.imagesInfo)
      if (res.meta.status === 404) {
        this.btnShow = false
        this.searchId = ''
        this.succeed = 0
        this.errors = 0
        this.pn = 0
        this.crawlingData = []
        this.$message.error(res.meta.msg)
        this.contentText.unshift(res.meta.msg)
      } else if (res.meta.status === 100) {
        this.contentText.unshift(`${this.crawlingData[i].photo.caption}${this.crawlingData[i].photo.photoUrl}---`)
        this.videoDownload(++i)
      } else if (res.meta.status === 201) {
        this.contentText.unshift(`${this.succeed}${this.crawlingData[i].photo.caption}${this.crawlingData[i].photo.photoUrl}---`)
        this.succeed++
        this.schedule = parseInt(this.succeed / Number(this.datasum) * 100)
        this.videoDownload(++i)
      } else {
        this.btnShow = true
        this.contentText.unshift(`${this.crawlingData[i].photo.caption}${this.crawlingData[i].photo.photoUrl}--${res.meta.msg}`)
        this.errors++
        this.videoDownload(++i)
      }
    }
  }
}

</script>

<style lang="scss" scoped>
.tag_seace_content {
    display: flex;
    .el-input {
      margin-right: 20px;
    }
  }
  .el-checkbox-group {
    .el-checkbox {
      margin: 10px;
    }
  }
  .el-radio-group {
    margin: 0 auto;
    .el-radio {
      margin: 5px;
    }
  }
  ::-webkit-scrollbar {
    display: none;
  }
   .tag_seace_content {
    display: flex;
    .el-input {
      margin-right: 20px;
    }
  }
  .el-tag {
      margin-right: 10px;
  }
  .user_button {
      margin-top: 20px;
  }
  .content {
    width: 1000px;
    // margin: 0 auto;
  }
  .description {
    margin-bottom: 20px;
  }
  .userInput {
    width: 150px;
    margin-left: 10px;
  }
  .content_box {
    border: 1px solid #dcdfe6;
    border-radius: 5px;
    color: #909399;
    padding: 0 10px;
    margin-top: 15px;
    p {
      word-break:break-word
    }
  }
  .content_msg {
    margin: 15px 0;
  }
</style>
