<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
          <!--  -->
            <el-row :gutter="20"> <!--  -->
                <el-col :span="12"> <!--  -->
                     <el-input placeholder="" v-model="queryInfo.query" @keyup.enter.native="searchSceneList"  clearable @clear="searchSceneList">
                    <el-select v-model="queryInfo.sort" slot="prepend" @change="selectChang" class="input-sort-select" placeholder="">
                      <el-option label="" :value="0"></el-option>
                      <el-option label="" :value="1"></el-option>
                      <el-option label="" :value="2"></el-option>
                    </el-select>
                        <el-select v-model="queryInfo.type" slot="prepend" @change="selectChang" class="input-with-select" placeholder="">
                          <el-option label="ID" :value="1">ID</el-option>
                          <el-option label="ID" :value="2">ID</el-option>
                          <el-option label="MD5" :value="3">MD5</el-option>
                          <el-option label="" :value="4"></el-option>
                          <el-option label="" :value="5"></el-option>
                        </el-select>
                        <el-button slot="append" icon="el-icon-search" @click="searchSceneList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="8">
                    <el-button type="primary" icon="el-icon-edit" @click="setUserClick"></el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="deleteScene"></el-button>
                </el-col>
            </el-row>

            <!--  -->
            <el-table ref="multipleTable" tooltip-effect="dark" :data="scenelist" @selection-change="handleSelectionChange" border stripe>
                <el-table-column align="center" type="selection" width="55"></el-table-column>
                <el-table-column align="center" type="index"></el-table-column>  <!--  -->
                <el-table-column align="center" label="" width="160" >
                    <template slot-scope="scope"> <!--  -->
                        <el-image v-if="scope.row.type == 1" style="width: 140px;cursor: pointer;" :preview-src-list="[path.images + scope.row.phone_path]" slot="error" :src="path.images + scope.row.phone_path" >
                          <i class="el-icon-picture-outline" style="font-size: 80px;cursor: pointer;"></i>
                        </el-image>
                        <video @click="showAuditDialog(path.video + scope.row.video_path)" v-else style="width: 140px;cursor: pointer;" :src="path.video + scope.row.video_path"></video>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="ID" prop="id"></el-table-column>
                <el-table-column align="center" label="ID" prop="user_id"></el-table-column>
                <el-table-column align="center" label="MD5" width="160" prop="md5"></el-table-column>
                <el-table-column align="center" label="" width="150">
                    <template slot-scope="scope">
                      <el-tag class="ele_tag" v-for="tag in scope.row.classify" :key="tag.id" closable
                      :disable-transitions="false" @close="handleprincipalClose(tag.id)">
                        {{tag.tag_name}}
                      </el-tag>
                      <el-button class="button-new-tag" size="small" @click="principalShowInput(scope.row.id)">+ </el-button>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="" prop="type">
                  <template slot-scope="scope">
                      <el-tag v-if="scope.row.type == 2"></el-tag>
                        <el-tag v-else type="success"></el-tag>
                  </template>
                </el-table-column>
                <el-table-column align="center" label="" width="140" prop="state">
                  <template slot-scope="scope">
                      <el-radio-group v-model="scope.row.state" size="mini" @change="sceneStatuChange(scope.row.id,scope.row.state)">
                        <el-radio  :label="1" border></el-radio>
                        <el-radio  :label="2" border></el-radio>
                      </el-radio-group>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="" width="180">
                    <template slot-scope="scope">
                      <p class="text_scene_desc">{{ scope.row.scene_desc }}</p>
                    </template>
                </el-table-column>

                <el-table-column align="center" label="">
                    <template slot-scope="scope">{{ scope.row.up_time | dateFormat }}</template>
                </el-table-column>

                <el-table-column align="center" label="" prop="likeSum"></el-table-column>
                <el-table-column align="center" label="" prop="collectSum"></el-table-column>
                <el-table-column align="center" label="" prop="commentSum"></el-table-column>
                <el-table-column align="center" label="">
                    <template slot-scope="scope">{{ scope.row.state_text_id == 0 || scope.row.state_text_id == null ? '' :  scope.row.state_text_id }}</template>
                </el-table-column>
             </el-table>
              <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="queryInfo.start" :page-sizes="[5, 10, 20, 50]" :page-size="queryInfo.limit"
            layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>
         <!--  -->
        <el-dialog title="" :visible.sync="addTagDialogVisible" width="50%" @close="addTagDialogClosed">
          <div class="tag_seace_content">
            <el-input @input="TagSearch" placeholder="" v-model="inputValue" clearable></el-input>
              <el-button type="primary" @click="addTagClick"></el-button>
          </div>
          <el-form :model="addTagForm" ref="addTagRef" label-width="70px">
            <el-checkbox-group v-model="addTagForm.tagPitch" size="mini">
              <el-checkbox v-for="item in tagList" :key="item.id" :label="item.id" border>{{ item.name }}</el-checkbox>
            </el-checkbox-group>
          </el-form>
        </el-dialog>
        <!--  -->
        <div class="audit_content" @keydown.esc="auditDialogVisible = false" v-show="auditDialogVisible">
            <video autoplay controls :src="videoPath"></video>
            <div @click="auditDialogVisible = false" class="audit_button">
                <i class="el-icon-close"></i>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      queryInfo: {
        // 
        query: '',
        // 
        type: 1,
        // 
        start: 1,
        // 
        limit: 5,
        // 
        sort: 0
      },
      // ip
      path: '',
      // 
      scenelist: [],
      // 
      total: 0,
      // 
      multipleSelection: [],
      // 
      inputValue: '',
      // 
      addTagDialogVisible: false,
      // 
      tagList: [],
      // 
      addTagForm: {
        scene_id: 0,
        tagPitch: []
      },
      // 
      auditDialogVisible: false,
      // 
      videoPath: ''
    }
  },
  created () {
    // 
    this.path = JSON.parse(window.sessionStorage.getItem('path'))
    this.getSceneList()
  },
  methods: {
    // 
    async getSceneList () {
      const { data: res } = await this.$http.get('/getSceneList', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.scenelist = res.data
      this.total = res.meta.sum
    },
    // 
    searchSceneList () {
      this.queryInfo.start = 1
      this.getSceneList()
    },
    // 
    selectChang () {
      this.queryInfo.start = 1
    },
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val.map(v => v.id)
    },
    //  pagesize 
    handleSizeChange (newSize) {
      this.queryInfo.limit = newSize
      this.getSceneList()
    },
    // 
    handleCurrentChange (newPage) {
      this.queryInfo.start = newPage
      this.getSceneList()
    },
    // 
    async setUserClick () {
      if (this.multipleSelection.length === 0) {
        return this.$message.error('')
      }
      const sate = await this.$prompt('id', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).catch(err => {
        return err
      })
      if (sate === 'cancel') {
        this.$message.info('')
      }
      const { data: res } = await this.$http.post('/setSceneUser', { scene_Arr: this.multipleSelection, user_id: sate.value.trim() })
      if (res.meta.status !== 201) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success('')
      // 
      this.getSceneList()
    },
    // 
    async deleteScene () {
      if (this.multipleSelection.length === 0) {
        return this.$message.error('')
      }
      const confirmResult = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => {
        return err
      })
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      // 
      const { data: res } = await this.$http.delete('/deleteScene', { data: { scene_id_arr: this.multipleSelection } })
      if (res.meta.status !== 201) {
        return this.$message.error(res.meta.msg)
      }
      this.getSceneList()
      this.$message.success('')
    },
    // 
    async sceneStatuChange (sceneId, status) {
      const { data: res } = await this.$http.put('/scenestatus', { scene_id: sceneId, status: status })
      if (res.meta.status !== 201) {
        this.$message.error(res.meta.msg)
        this.getSceneList()
        return
      }
      this.getSceneList()
      this.$message.success('')
    },
    // 
    async handleprincipalClose (tag) {
      const { data: res } = await this.$http.delete('/delsceneTag', { params: { id: tag } })
      if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
      this.getSceneList()
      this.$message.success('')
    },
    // 
    principalShowInput (sceneId, status) {
      this.addTagForm.scene_id = sceneId
      this.inputValue = ''
      this.tagList = []
      this.TagSearch()
      this.addTagDialogVisible = true
    },
    // 
    async TagSearch () {
      this.addTagForm.tagPitch = []
      const { data: res } = await this.$http.get(`/sceneTagSearch?query=${this.inputValue}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.tagList = res.data
    },
    // 
    addTagDialogClosed () {
      this.$refs.addTagRef.resetFields()
    },
    // 
    async addTagClick () {
      if (this.addTagForm.tagPitch.length === 0) {
        return this.$message.error('')
      }
      const { data: res } = await this.$http.post('/addSceneTags', this.addTagForm)
      if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
      this.addTagForm.tagPitch = []
      this.$message.success('')
      this.getSceneList()
    },
    // 
    showAuditDialog (path) {
      this.videoPath = path
      this.auditDialogVisible = true
    }
  },
  watch: {
    auditDialogVisible: function () {
      if (this.auditDialogVisible === false) {
        this.videoPath = ''
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .input-with-select {
    width: 120px;
    margin-right: 10px;
  }
  .text_scene_desc {
    // width: 160px;
    max-height: 280px;
    overflow: scroll;
    text-align: center;
  }
  .ele_tag {
    margin: 5px;
  }
  .tag_seace_content {
    display: flex;
    .el-input {
      margin-right: 20px;
    }
  }
  .el-checkbox-group {
    .el-checkbox {
      margin: 10px;
    }
  }
  .el-radio-group {
    margin: 0 auto;
    .el-radio {
      margin: 5px;
    }
  }
  ::-webkit-scrollbar {
    display: none;
  }
   .tag_seace_content {
    display: flex;
    .el-input {
      margin-right: 20px;
    }
  }
  ::-webkit-scrollbar {
    display: none;
  }
  .input-sort-select {
    width: 100px;
  }
    .audit_content {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba($color: #000000, $alpha: 0.6);
    z-index: 200;
    margin: 0 auto;
    width: 100%;
    height: 100vh;
    .audit_button {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        z-index: 220;
        position: absolute;
        right: 20px;
        top: 30px;
        background-color: rgba($color: #888888, $alpha: .6);
        cursor: pointer;
        i {
            font-size: 30px;
            color: rgb(172, 172, 172);
            text-align: center;
            line-height: 50px;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
        &:hover {
            background-color: rgba($color: #b4b4b4, $alpha: .6);
            i {
                color: #fff;
            }
        }
    }
    video {
      height: 100vh;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
  }
</style>
