<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row :gutter="20"> <!--  -->
                <el-col :span="8"> <!--  -->
                     <el-input placeholder="" v-model="queryInfo.query"  @keyup.enter.native="searchUserList" clearable @clear="searchUserList">
                        <el-button slot="append" icon="el-icon-search" @click="searchUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="10">
                    <el-button type="danger" @click="removeUserById(true)"></el-button>
                </el-col>
            </el-row>

            <!--  -->
            <el-table @selection-change="handleSelectionChange" :data="userlist" border stripe>
                <el-table-column align="center" type = "selection"></el-table-column>
                <el-table-column align="center" type="index"></el-table-column>  <!--  -->
                <el-table-column align="center" label="" prop="keyword"></el-table-column>
                <el-table-column align="center" label="" prop="counts"></el-table-column>

                <el-table-column align="center" label="" width="180px">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="setkeyword(scope.row.keyword)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(false, scope.row.keyword)"></el-button>
                    </template>
                </el-table-column>
             </el-table>
             <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="queryInfo.start" :page-sizes="[5, 10, 20, 50, 100]" :page-size="queryInfo.limit"
            layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        // 
        query: '',
        // 
        start: 1,
        // 
        limit: 10
      },
      // 
      userlist: [],
      // 
      total: 0,
      // 
      multipleSelection: []
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    // 
    async getUserList () {
      const { data: res } = await this.$http.get('/getkeyword', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.userlist = res.data
      this.total = res.meta.sum
    },
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val.map(v => v.keyword)
    },
    // 
    searchUserList () {
      this.queryInfo.start = 1
      this.getUserList()
    },
    //  pagesize 
    handleSizeChange (newSize) {
      this.queryInfo.limit = newSize
      this.getUserList()
    },
    // 
    handleCurrentChange (newPage) {
      this.queryInfo.start = newPage
      this.getUserList()
    },
    // 
    async setkeyword (keyword) {
      // 
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(async ({ value }) => {
        const { data: res } = await this.$http.put('/setkeyword', { keyword: keyword, newKeyword: value.trim() })
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        this.$message.success('')
        // 
        this.getUserList()
      }).catch(() => {
        this.$message.info('')
      })
    },
    // id
    async removeUserById (type, keyword) {
      if (type === true && this.multipleSelection.length === 0) {
        return this.$message.error('')
      }
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => {
        return err
      })
      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      if (keyword !== undefined) {
        const { data: res } = await this.$http.delete(`/delkeyword?keyword=${keyword}`)
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
      } else {
        const { data: res } = await this.$http.post('/delkeyword', { keyword_arr: this.multipleSelection })
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
      }
      this.$message.success('')
      // 
      this.getUserList()
    }
  }
}
</script>

<style lang="scss" scoped>
  .el-row {
    margin-bottom: 20px;
  }
</style>
