<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <!--  -->
            <el-row :gutter="20"> <!--  -->
                <el-col :span="8"> <!--  -->
                     <el-input placeholder="" v-model="queryInfo.query"  @keyup.enter.native="searchUserList" clearable @clear="searchUserList">
                        <el-button slot="append" icon="el-icon-search" @click="searchUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="10">
                    <el-button type="primary" @click="addDilogVisible = true"></el-button>
                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="userlist" border stripe>
                <el-table-column align="center" type="index"></el-table-column>  <!--  -->
                <el-table-column align="center" label="ID" prop="id"></el-table-column>
                <el-table-column align="center" label="" prop="username"></el-table-column>
                <el-table-column align="center" label="">
                    <template slot-scope="scope"> <!--  -->
                        {{ scope.row.create_time | dateFormat }}
                    </template>
                </el-table-column>
                <el-table-column align="center" label="">
                    <template slot-scope="scope"> <!--  -->
                        <el-switch v-model="scope.row.status" @change="userStateChanged(scope.row)"></el-switch>
                    </template>
                </el-table-column>
                <el-table-column align="center" label="" width="180px">
                    <template slot-scope="scope">
                        <!--  -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
                        <!--  -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
             </el-table>
             <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="queryInfo.start" :page-sizes="[5, 10, 20, 50, 100]" :page-size="queryInfo.limit"
            layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog
          title="" :visible.sync="addDilogVisible" width="50%" @close="addDialogClosed">
          <!--  -->
          <el-form :model="addForm" :rules="FormRules" ref="addFormRef" label-width="70px">
            <el-form-item label="" prop="username">
              <el-input v-model="addForm.username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input type="password" v-model="addForm.password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="status">
                    <el-switch v-model="addForm.status"></el-switch>
            </el-form-item>
          </el-form>
          <!--  -->
          <span slot="footer" class="dialog-footer">
            <el-button @click="addDilogVisible = false"> </el-button>
            <el-button type="primary" @click="addUser"> </el-button>
          </span>
        </el-dialog>

        <!--  -->
        <el-dialog
          title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
          <el-form :model="editForm" :rules="FormRules" ref="editFormRef" label-width="70px">
            <el-form-item label="" >
              <el-input v-model="editForm.username" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
              <el-input type="password" v-model="editForm.password"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editUserInfo"> </el-button>
          </span>
        </el-dialog>
    </div>
</template>

<script>
import MD5 from 'js-md5'
export default {
  data () {
    return {
      // 
      queryInfo: {
        // 
        query: '',
        // 
        start: 1,
        // 
        limit: 10
      },
      // 
      userlist: [],
      // 
      total: 0,
      // 
      addDilogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        status: false
      },
      // 
      FormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: '2~10', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 15, message: '5~15', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {
        id: '',
        username: '',
        password: ''
      }
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    // 
    async getUserList () {
      const { data: res } = await this.$http.get('/allAdminUser', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.userlist = res.data
      this.total = res.meta.sum
    },
    // 
    searchUserList () {
      this.queryInfo.start = 1
      this.getUserList()
    },
    // switch 
    async userStateChanged (userinfo) {
      const { data: res } = await this.$http.put(`/setAdminStatus?status=${userinfo.status === false ? 1 : 2}&id=${userinfo.id}`)
      if (res.meta.status !== 201) {
        userinfo.status = !userinfo.status
        return this.$message.error(res.meta.msg)
      }
      this.$message.success('')
      if (userinfo.id === JSON.parse(window.sessionStorage.getItem('userMsg')).user_id && userinfo.status === false) {
        window.sessionStorage.clear()
        this.$router.push('/')
      }
    },
    //  pagesize 
    handleSizeChange (newSize) {
      this.queryInfo.limit = newSize
      this.getUserList()
    },
    // 
    handleCurrentChange (newPage) {
      this.queryInfo.start = newPage
      this.getUserList()
    },
    //  
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser () {
      const data = {
        username: this.addForm.username,
        password: MD5(this.addForm.password),
        status: this.addForm.status === true ? 1 : 2
      }
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.post('/addAdminUser', data)
        if (res.meta.status !== 201) {
          this.$message.error(res.meta.msg)
          return
        }
        this.$message.success('')
        // 
        this.addDilogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog (user) {
      this.editForm.username = user.username
      this.editForm.id = user.id
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUserInfo () {
      // 
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        const data = {
          id: this.editForm.id,
          password: MD5(this.editForm.password)
        }
        const { data: res } = await this.$http.post('/editAdmin', data)
        if (res.meta.status !== 201) {
          return this.$message.error(res.meta.msg)
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // id
    async removeUserById (id) {
      // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => {
        return err
      })
      //  confirm
      //  cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(`/delAdmin?id=${id}`)
      if (res.meta.status !== 201) {
        return this.$message.error(res.meta.msg)
      }
      this.$message.success('')
      if (id === JSON.parse(window.sessionStorage.getItem('userMsg')).user_id) {
        window.sessionStorage.clear()
        this.$router.push('/')
        return
      }
      // 
      this.getUserList()
    }
  }
}
</script>

<style lang="scss" scoped>
  .el-row {
    margin-bottom: 20px;
  }
  h4 {
    margin-top: 30px;
  }
  .server {
    margin-top: 20px;
    .el-tag {
      margin-right: 10px;
      margin-bottom: 10px;
    }
  }
</style>
